import { BASE_URL, ACCESS_TOKEN } from '../constants';
import authHeader from "./auth-header";
import http from "./http-common";

const request = (options) => {
    const headers = new Headers({
        'Content-Type': 'application/json',
    })

    if(localStorage.getItem(ACCESS_TOKEN)) {
        headers.append('Authorization', 'Bearer ' + localStorage.getItem(ACCESS_TOKEN))
    }

    const defaults = {headers: headers};
    options = Object.assign({}, defaults, options);

    return fetch(options.url, options)
        .then(response =>
            response.json().then(json => {
                if(!response.ok) {
                    return Promise.reject(json);
                }
                return json;
            })
        );
};

export function signIn(signInRequest) {
    return request({
        url: BASE_URL + "/users/auth/signIn",
        method: 'POST',
        body: JSON.stringify(signInRequest)
    });
}

export function signUp(signUpRequest) {
    return request({
        url: BASE_URL + "/users/auth/signUp",
        method: 'POST',
        body: JSON.stringify(signUpRequest)
    });
}

export function getCurrentUser() {
    if(!localStorage.getItem(ACCESS_TOKEN)) {
        return Promise.reject("No access token set.");
    }

    return request({
        url: BASE_URL + "/users/id",
        method: 'GET'
    });
}

export function getUserProfile(id) {
    return request({
        url: BASE_URL + "/users/" + id,
        method: 'GET'
    });
}

export function logOut() {
    localStorage.removeItem("user");
}

class UtilsAPIService {

    getPublicContent() {
        return http.get("/admins/users");
    }

    getUserBoard(id) {
        return http.get(`/tutorials/${id}`, { headers: authHeader() });
    }
}

export default new UtilsAPIService();


